"use strict";

document.addEventListener("DOMContentLoaded", function () {
  //! login screen element
  const userEmailInput = document.getElementById("email");
  const userPasswordInput = document.getElementById("password");
  const loginBtn = document.getElementById("login");
  const displayLogin = document.querySelector(".login-content");

  //! dashBord page
  const dashboard = document.querySelector(".dashboard-container");

  // const account1 = {
  //   accountNumber: 1344234017,
  //   homeAddress: "4437 33rd Ave Seattle, WA 98108",
  //   mail: "test@test.com",
  //   movement: [],
  //   name: "Kim Tran",
  //   password: 12345,
  // };
  // user to login
  let currentUser;

  loginBtn.addEventListener("click", async function (e) {
    e.preventDefault();
    const allusers = [];

    const response = await fetch(
      "https://react-http-f3ac8-default-rtdb.firebaseio.com/mikiBank.json"
    );
    // check if userName exists
    const data = await response.json();

    allusers.push(data);

    currentUser = await allusers.find((element) => {
      const user = Object.values(element)[0];
      return user.mail === userEmailInput.value;
    });

    if (
      currentUser &&
      Object.values(currentUser)[0].password === +userPasswordInput.value
    ) {
      localStorage.setItem("allUsers", JSON.stringify({ allusers }));
      window.location.replace("dashboard.html");
    } else {
      console.log("error");
      // handle incorrect login credentials here
    }
  });
});
